import "../../css/screens/contact.css"


const Footer = () => {
    return (
        <div className="Footer">
            <div className="title">Contact Info</div>

            <div className="info">
                <div className="field">
                    <div className="phone-icon" />
                    <span>+447879957397</span>
                </div>
                <div className="field">
                    <div className="in-icon" />
                    <a href="https://www.linkedin.com/in/kunreddynaveen/" target="_blank"><span>Visit My Profile</span></a>
                </div>
                <div className="field">
                    <div className="email-icon" />
                    <span>kunreddy.naveen@gmail.com</span>
                </div>
            </div>
        </div>
    );
};

const Contact = () => {
    return (
        <div className="Contact">
            <div className="page-body">
                <div className="contentBox">Write some content here</div>
                <div className="form">
                    <form>
                        <input className="formElements" type="text" placeholder="Name"></input>
                        <input className="formElements" type="number" placeholder="Contact Number"></input>
                        <input className="formElements" type="email" placeholder="E mail ID"></input>
                        <textarea className="formElements formText" placeholder="Message"></textarea>
                        <input
                            className="formSubmit"
                            type="submit"
                            onClick={(e) => {
                                e.preventDefault();
                            }}
                        ></input>
                    </form>
                </div>
            </div>
            <Footer></Footer>
        </div>
    );
};

export default Contact;
