import "../../css/ui/profileLabel.css";
import { useSelector } from "react-redux";
import { RootState } from "../../../state/reducers";
import { ActionType } from "../../../const/action_type_const";

interface navBarProps {
    handler: Function;
}
interface userData {
    Name: string;
    isLoggedIn: boolean;
    Balance: number;
    Settings: any;
}

export default function ProfileLabel({ handler }: navBarProps) {
    const state: any = useSelector((state: RootState) => state);
    // const userData: userData = useSelector((state: RootState) => state.userDetails);
    const { userDetails, device } = state;



    return (
        //try make it a proper reusable component --so can be at 2 places  -one here itself and second onto mobile menu
        <div className="profile-label">
            {userDetails.isLoggedIn ? (
                device.isMobile ? (
                    <></>
                ) : (
                    <div>
                        {/* <div className="player-count">
                        Live Players: <span>12</span>
                    </div> */}
                        <div className="profile-section">
                            <div className="profile-avtar"></div>
                            <div className="avtar-border"></div>
                            <div className="profile-content">
                                <div className="userName">
                                    User: <span>{userDetails.Name}</span>
                                </div>
                                <div className="balance">
                                    Balance: <span>{userDetails.Balance}</span>
                                </div>
                                <div className="balance-inGame">
                                    Balance In Game: <span>{userDetails.InGame}</span>
                                </div>
                                <div className="btns">
                                    <div
                                        className="cashier"
                                        onClick={(e) => {
                                            e.preventDefault();
                                            handler({ data: {}, action: ActionType.SHOW_CASHIER });
                                        }}
                                    >
                                        Cashier
                                    </div>
                                    <div
                                        className="settings"
                                        onClick={(e) => {
                                            e.preventDefault();
                                            handler({ data: {}, action: ActionType.SHOW_SETTINGS });
                                        }}
                                    ></div>
                                    <div
                                        className="logout"
                                        onClick={(e) => {
                                            e.preventDefault();
                                            handler({ data: { Name: "", isLoggedIn: false, Balance: 0, Settings: {} }, action: ActionType.USER_LOGOUT });
                                        }}
                                    ></div>
                                </div>
                            </div>
                        </div>
                    </div>
                )
            ) : (
                <div className="login-section">
                    <div
                        className="btn"
                        onClick={(e) => {
                            e.preventDefault();
                            handler({ data: {}, action: ActionType.SHOW_LOGIN });
                        }}
                    >
                        Login
                    </div>

                    <div
                        className="btn"
                        onClick={(e) => {
                            e.preventDefault();
                            handler({ data: {}, action: ActionType.SHOW_SIGNUP });
                        }}
                    >
                        Sign Up
                    </div>
                </div>
            )}
        </div>
    );
}
