import "../../css/popUps/settings.css";
import { ActionType } from "../../../const/action_type_const";


interface settingsProps{
    handler:Function
}
export default function Settings({handler}:settingsProps){
    return(<div className="Settings">
        <div className="header">
                Settings
                <button
                    onClick={(e) => {
                        e.preventDefault();
                        handler({
                            data:{},
                            action:ActionType.HIDE_SETTINGS
                        });
                    }}
                >
                    X
                </button>
            </div>
    </div>)
}