import { useDispatch } from "react-redux";
import { bindActionCreators } from "redux";
import { actionCreators } from "../../../../state";
import { ActionType } from "../../../../const/action_type_const";

import "../../../css/games/fortune_spin/index.css";
import "../../../css/ui/buttons.css";
import "../../../css/ui/gameBoard.css";
import { useNavigate, useLocation } from "react-router-dom";
import { useSelector } from "react-redux";
import { RootState } from "../../../../state/reducers";
import SpinWheel from "./spinWheel";
import SelectChips from "./selectChips";
import BetPanel from "./betPanel";
import InfoBoard from "./infoBoard";
import Stats from "./stats";
import MobileMenu from "../../ui/mobileMenu";

import React, { useRef, useState } from "react";
export default function FortuneSpin() {
    const dispatch = useDispatch();
    const { UpdateLastBets } = bindActionCreators(actionCreators, dispatch);

    const state: any = useSelector((state: RootState) => state);
    const { userDetails, device, lastBets } = state;

    const history = useNavigate();
    const location = useLocation();
    const [betAmount, setBetAmount] = useState(0);
    const [showMenu, setShowMenu] = useState(false);
    const [stateText, setStateText] = useState({ text: "Chose Bet Amount", cl: "" });
    const target = useRef(11);

    // const wheelRef = React.createRef();
    const wheelRef: any = useRef();
    const statsRef: any = useRef();

    const startTheGame = (e: any) => {
        // console.log(e);
        console.log(wheelRef);
        setStateText({ text: "Spinning The Wheel", cl: "" });
        wheelRef.current.spinTheWheel();
    };

    const gameHandler = (data: any) => {
        switch (data.action) {
            case "betSelected":
                setBetAmount(data.amount);
                setStateText({ text: "Chose the slot", cl: "" });
                break;
            case "declareWin":
                console.log("==============declare the winner===============");
                console.log("bet at: ", target.current);
                console.log(statsRef);
                if (target.current == data.winSlot) {
                    setStateText({ text: `you won ${betAmount * 2}`, cl: "won" });
                    // data.push({ isWon: true, amount: betAmount*2, date: new Date() })
                    // if (!state.device.isMobile) statsRef.current.updateStats({ isWon: true, amount: betAmount * 2, date: new Date() });
                    UpdateLastBets({ isWon: true, amount: betAmount * 2, date: new Date() }, ActionType.PUSH_NEW_BET);
                } else {
                    setStateText({ text: "Oops you lost", cl: "lost" });
                    // data.push({ isWon: false, amount: betAmount, date: new Date() })
                    // if (!state.device.isMobile) statsRef.current.updateStats({ isWon: false, amount: betAmount, date: new Date() });
                    UpdateLastBets({ isWon: false, amount: betAmount, date: new Date() }, ActionType.PUSH_NEW_BET);
                }
                break;
            case "slotSelected":
                target.current = data.slot;
                break;
            // case "":
            //     break;
            default:
                break;
        }
    };
    return (
        <div className="fs">
            <div
                className="games-lobby-btn"
                onClick={(e) => {
                    e.preventDefault();
                    history("/casino");
                }}
            >
                Casino Lobby
            </div>
            <div className="layout portraight">
                <div className="fortuneSpin_gameBoard">
                    <SpinWheel ref={wheelRef} handler={gameHandler}></SpinWheel>
                    <SelectChips handler={gameHandler} device={device}></SelectChips>
                    <BetPanel amount={betAmount} handler={gameHandler} device={device}></BetPanel>
                    <InfoBoard text={stateText}></InfoBoard>
                    {!device.isMobile && <Stats></Stats>}
                    {device.isMobile && showMenu && <MobileMenu userDetails={userDetails} Stats={<Stats></Stats>}></MobileMenu>}
                    <div className="btn_style1 betBtn-Pos" onClick={startTheGame}>
                        Bet Ok
                    </div>
                    {device.isMobile && (
                        <div
                            className={showMenu ? "btn_swipe btn_swipe_rot" : "btn_swipe"}
                            onClick={(e) => {
                                e.preventDefault();
                                setShowMenu(!showMenu);
                            }}
                        ></div>
                    )}
                </div>
            </div>
        </div>
    );
}
