import React from "react";
import "../../../css/games/fortune_spin/spinWheel.css";

export default class SpinWheel extends React.Component {
    constructor(props) {
        super(props);
        this.spinDuration = 10000;
    }
    componentDidMount() {
        this.wheelBase = document.getElementsByClassName("spinWheelGraphics");
    }
    spinTheWheel(x) {
        let v = Math.floor(Math.random() * (10 - 0));
        // let v = 3;
        console.log("stop the wheel at: " + v);
        this.wheelBase[0].removeAttribute("spin-wheel");
        setTimeout(() => {
            this.wheelBase[0].setAttribute("spin-wheel", v);
        }, 10);
        setTimeout(() => {
            // console.log("declare win");
            this.declareWin(v)
        }, this.spinDuration);
    }
    declareWin(winSlot){
        this.props.handler({action:"declareWin",winSlot:Number(winSlot)})
    }
    render() {
        return (
            <div className="fortuneWheel">
                {/* <button
                    onClick={(e) => {
                        e.preventDefault();
                        let v = Math.floor(Math.random() * (10 - 0));
                        // let v = 3;
                        console.log("stop the wheel at: " + v);
                        this.wheelBase[0].removeAttribute("spin-wheel");
                        setTimeout(() => {
                            this.wheelBase[0].setAttribute("spin-wheel", v);
                        }, 10);
                        setTimeout(() => {
                            console.log("declare win");
                        }, this.spinDuration);
                    }}
                >
                    Start
                </button> */}
                <div className="spinWheelGraphics">
                    <div className="wedgePlate">
                        <ul className="wedgeContainer">
                            <li className="wedge"></li>
                            <li className="wedge"></li>
                            <li className="wedge"></li>
                            <li className="wedge"></li>
                            <li className="wedge"></li>
                            <li className="wedge"></li>
                            <li className="wedge"></li>
                            <li className="wedge"></li>
                            <li className="wedge"></li>
                            <li className="wedge"></li>
                        </ul>
                    </div>
                    <div className="numberPlate">
                        <ul className="numberContainer">
                            <li className="number">
                                <span>0</span>
                            </li>
                            <li className="number">
                                <span>1</span>
                            </li>
                            <li className="number">
                                <span>2</span>
                            </li>
                            <li className="number">
                                <span>3</span>
                            </li>
                            <li className="number">
                                <span>4</span>
                            </li>
                            <li className="number">
                                <span>5</span>
                            </li>
                            <li className="number">
                                <span>6</span>
                            </li>
                            <li className="number">
                                <span>7</span>
                            </li>
                            <li className="number">
                                <span>8</span>
                            </li>
                            <li className="number">
                                <span>9</span>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="wheelBase"></div>
                <div className="wheelOuter"></div>
            </div>
        );
    }
}
