import { ActionType } from "../../const/action_type_const/index";
import { Action } from "../action_types";

interface lastBets {
    data: {
        isWon: boolean;
        amount: number;
        date: string;
    }[];
}
const betsData: lastBets["data"] = [
    {
        isWon: false,
        amount: 0,
        date: "0",
    },
];
const lastBets = (state: any = [], action: any) => {
    console.log("update user details", action);
    switch (action.type) {
        case ActionType.INIT_LAST_BETS:
            return (state = action.payload);
        case ActionType.PUSH_NEW_BET:
            console.log("push the new bet transaction: ", action.payload);
            let data = state;
            if (data.length < 5) {
                data.push(action.payload);
            } else {
                data.shift();
                data.push(action.payload);
            }
            return (state = [...data]);
        default:
            return state;
    }
};

export default lastBets;
