import { useState } from "react";

export default function Poker() {
    const [showFullScreen, setShowFullScreen] = useState(false);

    return (
        <div className={showFullScreen ? "mobile" : "desktop"}>
            Poker Game Here
            <button
                onClick={(e) => {
                    e.preventDefault();
                    setShowFullScreen(!showFullScreen);
                }}
            >
                Toggle
            </button>
        </div>
    );
}
