import "../../css/popUps/signUp.css";
import { ActionType } from "../../../const/action_type_const";


interface signUpProps{
    handler:Function
}
export default function SignUp({handler}:signUpProps){
    return(<div className="SignUp">
        <div className="header">
                SignUp
                <button
                    onClick={(e) => {
                        e.preventDefault();
                        handler({
                            data:{},
                            action:ActionType.HIDE_SIGNUP
                        });
                    }}
                >
                    X
                </button>
            </div>
    </div>)
}