import "./App.css";
import { useDispatch } from "react-redux";
import { bindActionCreators } from "redux";
import { actionCreators } from "./state";
import { ActionType } from "./const/action_type_const";
import { useEffect, useState } from "react";
import { RootState } from "./state/reducers";
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";

import NavBar from "./components/ts/ui/navBar";
import ProfileLabel from "./components/ts/ui/profileLabel"

import Home from "./components/ts/screens/home";
import Contact from "./components/ts/screens/contact";
import Casino from "./components/ts/screens/casino";
import LiveCasino from "./components/ts/screens/liveCasino";
import SportsBet from "./components/ts/screens/sportsBet";

import Plinko from "./components/ts/games/plinko";
import Poker from "./components/ts/games/poker";
import Roulette_American from "./components/ts/games/roulette_american";
import InsideOut from "./components/ts/games/inside-out";
import Roulette_European from "./components/ts/games/roulette_european";
import FortuneSpin from "./components/ts/games/fortune_spin";
import FortuneMotif from "./components/ts/games/fortune_motif";
import ComboSpin from "./components/ts/games/combo_spin";


import Login from "./components/ts/popUps/login";
import SignUp from "./components/ts/popUps/signUp";
import Cashier from "./components/ts/popUps/cashier";
import Settings from "./components/ts/popUps/settings";
import AvatarPopup from "./components/ts/popUps/AvatarPopup";

function App() {
    const dispatch = useDispatch();
    const [showLogin, setShowLogin] = useState(false);
    const [showSignUp, setShowSignUp] = useState(false);
    const [showCashier, setShowCashier] = useState(false);
    const [showSettings, setShowSettings] = useState(false);
    const { InitGames,UpdateUser } = bindActionCreators(actionCreators, dispatch);

    console.log(process.env.REACT_APP_VARA);

    const userDataHandler = (data:any)=>{
        popUpHandler(data.action);
        console.log("user data from server is ");
        console.log(data);
        switch(data.action){
            case ActionType.USER_LOGIN:
                UpdateUser(data.data,data.action);
                break;
            case ActionType.USER_LOGOUT:
                UpdateUser(data.data,data.action);
                break;
            case ActionType.USER_SETTINGS_CHANGED:
            default:
                InitGames([
                    { id: 1, name: "roulette_american", isActive: true,statusText:"",type:"Real Money" },
                    { id: 2, name: "roulette_european", isActive: true,statusText:"",type:"Real Money" },
                    { id: 3, name: "inside_out", isActive: true,statusText:"",type:"Real Money" },
                    { id: 4, name: "poker", isActive: false,statusText:"Coming Soon",type:"Real Money" },
                    { id: 5, name: "plinko", isActive: true,statusText:"",type:"Real Money" },
                ])
                break;    

        }
    }
    
    const popUpHandler = (action:String) => {
        console.log(action);
        switch(action){
            case ActionType.SHOW_LOGIN:
                setShowLogin(true);
                break;
            case ActionType.HIDE_LOGIN:
                setShowLogin(false);
                break;
            case ActionType.SHOW_SIGNUP:
                setShowSignUp(true);
                break;
            case ActionType.HIDE_SIGNUP:
                setShowSignUp(false);
                break;
            case ActionType.SHOW_SETTINGS:
                setShowSettings(true);
                break;
            case ActionType.HIDE_SETTINGS:
                setShowSettings(false);
                break;
            case ActionType.SHOW_CASHIER:
                setShowCashier(true);
                break;
            case ActionType.HIDE_CASHIER:
                setShowCashier(false);
                break;
            default:
                break;    
        }
    };
    useEffect(() => {}, []);

    return (
        <div className="App">
            <NavBar handler={userDataHandler}></NavBar>
            <ProfileLabel handler={userDataHandler}></ProfileLabel>
            <Routes>
                <Route path="/" element={<Home></Home>}></Route>
                <Route path="/casino" element={<Casino></Casino>}></Route>
                <Route path={"/casino/plinko"} element={<Plinko></Plinko>}></Route>
                <Route path={"/casino/poker"} element={<Poker></Poker>}></Route>
                <Route path={"/casino/roulette_american"} element={<Roulette_American></Roulette_American>}></Route>
                <Route path={"/casino/roulette_european"} element={<Roulette_European></Roulette_European>}></Route>
                <Route path={"/casino/inside_out"} element={<InsideOut></InsideOut>}></Route>
                <Route path={"/casino/fortune_spin"} element={<FortuneSpin></FortuneSpin>}></Route>
                <Route path={"/casino/fortune_motif"} element={<FortuneMotif></FortuneMotif>}></Route>
                <Route path={"/casino/combo_spin"} element={<ComboSpin></ComboSpin>}></Route>
                <Route path="/casinoLive" element={<LiveCasino></LiveCasino>}></Route>
                <Route path="/sportsBet" element={<SportsBet></SportsBet>}></Route>
                <Route path="/contact" element={<Contact></Contact>}></Route>
            </Routes>
            {showLogin && <Login handler={userDataHandler}></Login>}
            {showSignUp && <SignUp handler={userDataHandler}></SignUp>}
            {showCashier && <Cashier handler={userDataHandler}></Cashier>}
            {showSettings && <Settings handler={userDataHandler}></Settings>}
            {/* <AvatarPopup></AvatarPopup> */}
        </div>
    );
}

export default App;
