import "../../../css/games/combo_spin/index.css";
import "../../../css/ui/buttons.css";
import "../../../css/ui/gameBoard.css";
import { useNavigate, useLocation } from "react-router-dom";

import { useState } from "react";

export default function ComboSpin() {
    const history = useNavigate();
    const location = useLocation();
    return (
        <div className="fs">
            <div
                className="games-lobby-btn"
                onClick={(e) => {
                    e.preventDefault();
                    history("/casino");
                }}
            >
                Games Lobby
            </div>
            <div className="layout portraight">
                <div className="comboSpin_gameBoard">Combination spin game board  3 in circles --should be portraight</div>
            </div>
        </div>
    );
}
