import { ActionType } from "../../const/action_type_const/index";
import { Action } from "../action_types";

interface userData {
    Name: string;
    isLoggedIn:boolean;
    Balance: number;
    InGame: number;
    Settings: any;
}
const userData = {
    Name: "",
    isLoggedIn:false,
    Balance: 0,
    InGame: 0,
    Settings: "",
};
const userDetails = (state: any = userData, action: Action) => {
    switch (action.type) {
        case ActionType.USER_LOGIN:
            return (state = action.payload);
        case ActionType.USER_LOGOUT:
            console.log("update user state for logout");
            return state = action.payload;
        case ActionType.USER_SETTINGS_CHANGED:
            console.log("update user state for settings changed");
            return state
        default:
            return state;
    }
};

export default userDetails;
