import "../../css/ui/navBar.css";
import { useEffect, useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useSelector } from "react-redux";
import { RootState } from "../../../state/reducers";
import { ActionType } from "../../../const/action_type_const";

interface navBarProps {
    handler: Function;
}
interface userData {
    Name: string;
    isLoggedIn: boolean;
    Balance: number;
    Settings: any;
}
export default function NavBar({ handler }: navBarProps) {
    const [navBtn, setNavBtn] = useState(1);
    const [isLoggedIn, setIsloggedIn] = useState(false);
    const history = useNavigate();
    const location = useLocation();
    const userData: userData = useSelector((state: RootState) => state.userDetails);
    console.log(userData);
    const navigationHandler = (path: string) => {
        // console.log(path);
        switch (path) {
            case "home":
                history("/");
                break;
            case "casino":
                history("/casino");
                break;
            case "casinoLive":
                history("/casinoLive");
                break;
            case "sportsBet":
                history("/sportsBet");
                break;
            case "contact":
                history("/contact");
                break;
            default:
                history("/");

                break;
        }
    };

    useEffect(() => {
        // console.log(location.pathname);
        switch (location.pathname) {
            case "/":
                setNavBtn(1);

                break;
            case "/casino":
                setNavBtn(2);
                break;
            case "/sportsBet":
                setNavBtn(3);
                break;
            case "/casinoLive":
                setNavBtn(4);
                break;
            case "/contact":
                setNavBtn(5);
                break;
            default:
                break;
        }
    }, [location]);
    return (
        <div className="NavBar">
            <div className="logo">{/* n'<span style={{color:"#ffe555"}}>Bets</span> */}</div>
            {/* <div className="title"></div> */}
            <div className="links">
                <div
                    className={navBtn === 1 ? "active" : "inActive"}
                    onClick={(e) => {
                        navigationHandler("home");
                        // setNavBtn(1);
                    }}
                >
                    Home
                </div>
                <div
                    className={navBtn === 2 ? "active" : "inActive"}
                    onClick={(e) => {
                        navigationHandler("casino");
                        // setNavBtn(2);
                    }}
                >
                    Casino
                </div>
                <div
                    className={navBtn === 3 ? "active" : "inActive"}
                    onClick={(e) => {
                        navigationHandler("sportsBet");
                        // setNavBtn(2);
                    }}
                >
                    Sports Bet
                </div>
                <div
                    className={navBtn === 4 ? "active" : "inActive"}
                    onClick={(e) => {
                        navigationHandler("casinoLive");
                        // setNavBtn(2);
                    }}
                >
                    Casino Live
                </div>
                <div
                    className={navBtn === 5 ? "active" : "inActive"}
                    onClick={(e) => {
                        navigationHandler("contact");
                        // setNavBtn(3);
                    }}
                >
                    Contact
                </div>
            </div>
            {/* {userData.isLoggedIn ? (
                <div>
                    <div className="player-count">
                        Live Players: <span>12</span>
                    </div>
                    <div className="profile-section">
                        <div className="profile-avtar"></div>
                        <div className="avtar-border"></div>
                        <div className="profile-content">
                            <div className="userName">
                                User: <span>{userData.Name}</span>
                            </div>
                            <div className="balance">
                                Balance: <span>{userData.Balance}</span>
                            </div>
                            <div className="balance-inGame">
                                Balance In Game: <span>{userData.Balance}</span>
                            </div>
                            <div className="btns">
                                <div
                                    className="cashier"
                                    onClick={(e) => {
                                        e.preventDefault();
                                        handler({ data: {}, action: ActionType.SHOW_CASHIER });
                                    }}
                                >
                                    Cashier
                                </div>
                                <div
                                    className="settings"
                                    onClick={(e) => {
                                        e.preventDefault();
                                        handler({ data: {}, action: ActionType.SHOW_SETTINGS });
                                    }}
                                ></div>
                                <div
                                    className="logout"
                                    onClick={(e) => {
                                        e.preventDefault();
                                        handler({ data: { Name: "", isLoggedIn: false, Balance: 0, Settings: {} }, action: ActionType.USER_LOGOUT });
                                    }}
                                ></div>
                            </div>
                        </div>
                    </div>
                </div>
            ) : (
                <div className="login-section">
                    <div
                        className="btn"
                        onClick={(e) => {
                            e.preventDefault();
                            handler({ data: {}, action: ActionType.SHOW_LOGIN });
                        }}
                    >
                        Login
                    </div>

                    <div
                        className="btn"
                        onClick={(e) => {
                            e.preventDefault();
                            handler({ data: {}, action: ActionType.SHOW_SIGNUP });
                        }}
                    >
                        Sign Up
                    </div>
                </div>
            )} */}
        </div>
    );
}
