import { ActionType } from "../../const/action_type_const/index";
import { Action } from "../action_types";

interface gameData {
    data: {
        id: number;
        name: string;
        isActive: boolean;
        statusText:string;
        type:string
    }[];
}
const games:gameData["data"] = [
    { id: 1, name: "roulette_american", isActive: true,statusText:"",type:"Real Money" },
    { id: 2, name: "roulette_european", isActive: true,statusText:"",type:"Real Money" },
    { id: 3, name: "inside_out", isActive: true,statusText:"",type:"Real Money" },
    { id: 4, name: "poker", isActive: true,statusText:"",type:"Real Money" },
    { id: 5, name: "plinko", isActive: true,statusText:"",type:"Real Money" },
    { id: 6, name: "fortune_spin", isActive: true,statusText:"",type:"Real Money" },
    { id: 7, name: "fortune_motif", isActive: true,statusText:"",type:"Real Money" },
    { id: 8, name: "combo_spin", isActive: true,statusText:"",type:"Real Money" },
];
const gamesData = (state: any = games, action: Action) => {
    console.log("update game details",action);
    switch (action.type) {
        case ActionType.INIT_GAMES:
            return (state = action.payload);
        default:
            return state;
    }
};
