import "../../css/popUps/cashier.css";
import { ActionType } from "../../../const/action_type_const";


interface cashierProps{
    handler:Function
}
export default function Cashier({handler}:cashierProps){
    return(<div className="Cashier">
        <div className="header">
                Cashier
                <button
                    onClick={(e) => {
                        e.preventDefault();
                        handler({
                            data:{},
                            action:ActionType.HIDE_CASHIER
                        });
                    }}
                >
                    X
                </button>
            </div>
    </div>)
}