import { Dispatch } from "redux";
import { ActionType } from "../../const/action_type_const";
import { Action } from "../action_types/index";

interface gameData {
    data: {
        id: number;
        name: string;
        isActive: boolean;
        statusText:string;
        type:string
    }[];
}
interface userData {
    Name: string;
    isLoggedIn:boolean;
    Balance: number;
    InGame: number;
    Settings: any;
}
interface lastBets {
    data:{
        isWon: boolean;
        amount: number;
        date: string;
    }[] 
}

export const InitGames = (data: gameData["data"]) => {
    return (dispatch: Dispatch<Action>) => {
        dispatch({
            type: ActionType.INIT_GAMES,
            payload: data,
        });
    };
};

export const UpdateUser = (data: userData,type: any) => {
    return (dispatch: Dispatch<Action>) => {
        dispatch({
            type: type,
            payload: data,
        });
    };
};
export const UpdateLastBets = (data:any ,type: any) => {
    return (dispatch: Dispatch<Action>) => {
        dispatch({
            type: type,
            payload: data,
        });
    };
};
