


const isMobile = window.matchMedia("(pointer: coarse) and (hover: none)").matches;
// const isMobile2 = window.matchMedia("(max-width: 768px)").matches;  //for tablets

const isMobile2 = window.matchMedia("(max-width: 480px)").matches;

const isMobile3 = window.matchMedia("(min-width: 481px) and (max-width: 768px)").matches;


// const isMobile3 =  @media (min-width: 481px) and (max-width: 767px)

// export const SeatLayout=  (isMobile || isMobile2)? SeatLayout_mobile: SeatLayout_desktop;

let code = {code:3,isMobile:false};

if (isMobile || isMobile2 || isMobile3) {
    code = isMobile2 ? {code:1,isMobile:true} : {code:2,isMobile:true};
} else {
    code = {code:3,isMobile:false};
}
// tablet: 2, mobile: 1, desktop:3

interface code {
    code:number,
    isMobile:boolean
}
const device = (state:code=code,action:any="")=>{
    return state;
}

export default device;