import "../../css/screens/casino.css";
import { useSelector } from "react-redux";
import { RootState } from "../../../state/reducers";
import GameCard from "../ui/gameCard";
import { useEffect } from "react";

interface gameData {
    data: {
        id: number;
        name: string;
        isActive: boolean;
        statusText: string;
    }[];
}
export default function Casino() {
    const games: gameData["data"] = useSelector((state: RootState) => state.gamesData);

    const GamesCards: any = () => {
        return games.map((game) => {
            return <GameCard id={game.id} name={game.name} isActive={game.isActive} statusText={game.statusText}></GameCard>;
        });
    };

    useEffect(()=>{
        
    },[])
    return (
        <div className="Games">
            <h1>Play Zone</h1>
            <h2>Play and Earn Real Money</h2>
            <div className="game-cards-grid">
                <GamesCards></GamesCards>
            </div>
            <h1>Fun Zone</h1>
            <h2>Unlimited free fun with Play Money</h2>
            <div className="game-cards-grid">
                <GamesCards></GamesCards>
            </div>
        </div>
    );
}
