import "../../css/ui/gameCard.css";
import { useNavigate } from "react-router-dom";

interface gameData {
    id: number;
    name: string;
    isActive: boolean;
    statusText: string;
}

export default function GameCard({ id, name, isActive, statusText }: gameData) {
    const history = useNavigate();

    const gameHandler = (game: any) => {
        switch (game) {
            case "poker":
                history("/casino/poker");
                break;
            case "inside_out":
                history("/casino/inside_out");
                break;
            case "roulette_american":
                history("/casino/roulette_american");
                break;
            case "roulette_european":
                history("/casino/roulette_european");
                break;
            case "fortune_spin":
                history("/casino/fortune_spin");
                break;
            case "fortune_motif":
                history("/casino/fortune_motif");
                break;
            case "combo_spin":
                history("/casino/combo_spin");
                break;
            case "plinko":
                history("/casino/plinko");
                break;
            default:
                break;
        }
    };
    return (
        <div
            className={isActive ? `GameCard ${name}` : `GameCard ${name} inActive`}
            onClick={(e) => {
                e.preventDefault();
                console.log("open the game: ",name);
                isActive? gameHandler(name):alert("Game not Working");
            }}
        >
            <div className={isActive ? "show-status" : "statu-text"}>{statusText}</div>
        </div>
    );
}



