import "../../../css/games/plinko/index.css";
import "../../../css/ui/buttons.css";
import "../../../css/ui/gameBoard.css";
import { useNavigate, useLocation } from "react-router-dom";

import { useState } from "react";
export default function Plinko() {
    const history = useNavigate();
    const location = useLocation();
    return (
        <div className="fs">
            <div
                className="games-lobby-btn"
                onClick={(e) => {
                    e.preventDefault();
                    history("/casino");
                }}
            >
                Games Lobby
            </div>
            <div className="layout portraight">
                <div className="plinko_gameBoard">Plinko game board --should be portraight</div>
            </div>
        </div>
    );
}
