import "../../css/popUps/login.css";
import { ActionType } from "../../../const/action_type_const";
import { Api_Methods } from "../../api";
import { useState } from "react";

interface loginProps {
    handler: Function;
}

export default function Login({ handler }: loginProps) {
    const [user, setUser] = useState("");
    const [key, setKey] = useState("");
    return (
        <div className="Login">
            <div className="header">
                Login
                <button
                    onClick={(e) => {
                        e.preventDefault();
                        handler({
                            data: {},
                            action: ActionType.HIDE_LOGIN,
                        });
                    }}
                >
                    X
                </button>
            </div>
            <div className="content">
                <form>
                    <input
                        type="text"
                        placeholder="User Name"
                        onChange={(e) => {
                            e.preventDefault();
                            setUser(e.target.value);
                        }}
                    ></input>
                    <input
                        type="password"
                        placeholder="Password"
                        onChange={(e) => {
                            e.preventDefault();
                            setKey(e.target.value);
                        }}
                    ></input>
                    <input
                        className="formSubmit"
                        type="submit"
                        onClick={(e) => {
                            e.preventDefault();
                            // console.log(Api_Methods);
                            Api_Methods.login({ userName: user, password: key })
                                .then((response) => {
                                    console.log(response);
                                    handler({
                                        data: { Name: response.data.userName, isLoggedIn: true, Balance: response.data.balance, InGame:response.data.balanceInGame,Settings: {} },
                                        action: ActionType.USER_LOGIN,
                                    });
                                    handler({
                                        data: {},
                                        action: ActionType.HIDE_LOGIN,
                                    });
                                })
                                .catch((error) => {
                                    console.log(error);
                                });
                        }}
                    ></input>
                </form>
            </div>
        </div>
    );
}
