import { useState } from "react";
// import { useSelector } from "react-redux";
// import { RootState } from "../../../../state/reducers";

import "../../../css/games/fortune_spin/selectChips.css";

export default function SelectChips({ handler,device }: any) {


    // const device:any = useSelector((state: RootState) => state.device);

    const [select, setSelect] = useState(0);
    const chipsData = [
        [1, 5, 10],
        [50, 100, 500],
        [1000, 5000, 1],
    ];
    // const [isMobile, setIsMobile] = useState(device.isMobile);
    const [showChips, setShowChips] = useState(false);
    const slectChip = (e: any) => {
        console.log(e.target.dataset.chip);
        setSelect(Number(e.target.dataset.chip));
        handler({ action: "betSelected", amount: Number(e.target.dataset.chip) });
    };

    return (
        <div className="chipsPanel">
            <div className="header">
                You are betting <span>{select}</span>
            </div>
            <div className={device.isMobile ? (showChips ? "show" : "hide") : "show"}>
                {(() => {
                    return chipsData.map((row: any, i: any) => {
                        return (
                            <>
                                <div key={i} className="row">
                                    {(() => {
                                        return row.map((col: any, j: any) => {
                                            let cl = select == Number(col) ? `chip chip_${col} active` : `chip chip_${col}`;
                                            return (
                                                <div key={i + "" + j} id={i + "" + j} className="column">
                                                    <div key={i + "" + j + 2} className={cl} data-chip={col} onClick={slectChip}></div>
                                                </div>
                                            );
                                        });
                                    })()}
                                </div>
                            </>
                        );
                    });
                })()}
            </div>
            
            {device.isMobile && (
                <div
                    className={showChips ? "base flip" : "base"}
                    onClick={(e) => {
                        e.preventDefault();
                        setShowChips(!showChips);
                    }}
                ></div>
            )}
        </div>
    );
}
