import "../../css/ui/mobileMenu.css";
import "../../css/ui/profileLabel.css";

export default function MobileMenu({ userDetails, Stats }: any) {
    const MobileProfile = () => {
        return (
            <>
                <div className="profile-section">
                    <div className="avtar-border">
                        <div className="profile-avtar"></div>
                    </div>
                    <div className="profile-content">
                        <div className="userName">
                            User: <span>{userDetails.Name}</span>
                        </div>
                        <div className="balance">
                            Balance: <span>{userDetails.Balance}</span>
                        </div>
                        <div className="balance-inGame">
                            In Game: <span>{userDetails.InGame}</span>
                        </div>
                        {/* <div className="btns">
                                    <div
                                        className="cashier"
                                        onClick={(e) => {
                                            e.preventDefault();
                                            handler({ data: {}, action: ActionType.SHOW_CASHIER });
                                        }}
                                    >
                                        Cashier
                                    </div>
                                    <div
                                        className="settings"
                                        onClick={(e) => {
                                            e.preventDefault();
                                            handler({ data: {}, action: ActionType.SHOW_SETTINGS });
                                        }}
                                    ></div>
                                    <div
                                        className="logout"
                                        onClick={(e) => {
                                            e.preventDefault();
                                            handler({ data: { Name: "", isLoggedIn: false, Balance: 0, Settings: {} }, action: ActionType.USER_LOGOUT });
                                        }}
                                    ></div>
                                </div> */}
                    </div>
                </div>
            </>
        );
    };

    return (
        <div className="mobileMenu">
            {userDetails.isLoggedIn && <MobileProfile></MobileProfile>}
            <div>{Stats}</div>
        </div>
    );
}
